<template>
  <v-app>
    <v-navigation-drawer
    temporary
    v-model="sideNav"
    light
    >
      <v-list>
        <v-list-tile
          v-for="nav in navItems"
          :key="nav.name"
          router
          :to="nav.path"
        >
          <v-list-tile-action >
            <v-icon left>
              {{ nav.icon }}
            </v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            {{ nav.name }}
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar dark class="primary">
      <v-toolbar-side-icon
        @click.native.stop="sideNav = !sideNav"
        class="hidden-md-and-up"></v-toolbar-side-icon>

      <v-toolbar-title class="hidden-sm-and-down">
        <router-link
          to="/"
          tag="span"
          style="cursor: pointer;"
        >
          ACM's Collaboration Resources
        </router-link>
      </v-toolbar-title>

      <v-toolbar-title class="hidden-md-and-up">
        <router-link
          to="/"
          tag="span"
          style="cursor: pointer;"
        >
          {{ titleShort }}
        </router-link>
      </v-toolbar-title>
    </v-toolbar>

    <main>
      <router-view></router-view>
    </main>

    <v-footer class="primary" dark>
      <v-spacer></v-spacer>
      <div class="white--text"><a class="white--text" href="http://acmucr.org">ACM@UCR</a> &copy; 2017 Alpha Release 0.0.2</div>
    </v-footer>

  </v-app>
</template>

<script>
  export default {
    computed: {
      currentOrg () {
        return this.$store.getters.currentOrg
      },
      navItems () {
        let navItems = [  
          {
            name: 'Login',
            icon: 'vpn_key',
            path: '/login'
          },
          {
            name: 'Register',
            icon: 'group_add',
            path: '/register',
          }
        ]

        if (this.isAuthenticated) {
          navItems = [
            {
              name: 'View Events',
              icon: 'list',
              path: '/events'
            },
            {
              name: 'Your Events',
              icon: 'today',
              path: '/events'
            },
            {
              name: 'Organization Profile',
              icon: 'person',
              path: '/profile'
            },
          ]
        }
        navItems.push({
              name: 'Resources',
              icon: 'lightbulb_outline',
              path: '/resources'
            })
        return navItems
      },
      isAuthenticated () {
        return this.$store.getters.currentOrg !== null
          && this.$store.getters.currentOrg !== undefined
      }
    },
    data () {
      return {
        title: 'ACM\'s Collaboration Experience',
        titleShort: 'ACE',
        sideNav: false,
      }
    }
  }
</script>



