<template>
   <v-container grid-list-md>
    <v-layout row wrap>
      <v-flex md2 class="text-md-center">
        <v-avatar
            size="250px"
            class="grey lighten-4"
          >
            <img :src="org.image" alt="avatar">
          </v-avatar>
      </v-flex>
      <v-flex md10>
        <h2 id="org-name">{{ org.name }}</h2>
        <p v-if="org.clubroom.length !== 0"><v-icon>home</v-icon> <strong>Clubroom:</strong> {{ org.clubroom }}</p>
        <p><v-icon>mail_outline</v-icon> <strong>Email:</strong> {{ org.email }}</p>
        <p><v-icon>contacts</v-icon> <strong>Board Meetings:</strong> {{ org.boardMeeting.day }} from {{ org.boardMeeting.time }}</p>
        <p>{{ org.summary }}</p>
      </v-flex>

      <v-tabs
        dark
        fixed
        centered
        icons
      >
        <v-tabs-bar>
          <v-tabs-slider></v-tabs-slider>
          <v-tabs-item key="officers" href="#tab-0">
            <v-icon>people</v-icon>
            Officers
          </v-tabs-item>
          <v-tabs-item key="events" href="#tab-1">
            <v-icon>event_note</v-icon>
            Events
          </v-tabs-item>
        </v-tabs-bar>

        <v-tabs-items>
          <v-tabs-content key="0" id="tab-0">
            <v-card flat>
              <v-flex md12 class="mt-2">
                <v-card>
                  <v-card-title primary-title>
                    <div>
                      <h3 class="headline mb-0">Officers</h3>
                    </div>
                  </v-card-title>
                  <v-card-text>
                    <v-data-table
                      hide-actions
                      :items="org.officers"
                      :headers="officerHeaders"
                    >
                      <template slot="items" scope="props">
                        <td>{{ props.item.position }}</td>
                        <td>{{ props.item.name }}</td>
                        <td>{{ props.item.email }}</td>
                      </template>
                    </v-data-table>
                  </v-card-text>
                </v-card>
              </v-flex>
            </v-card>
          </v-tabs-content>
        </v-tabs-items>

        <v-tabs-items>
          <v-tabs-content key="1" id="tab-1">
            <v-card flat>
              <v-flex md12 class="mt-2">
                <v-card>
                  <v-card-title primary-title>
                    <div>
                      <h3 class="headline mb-0">Upcoming Events</h3>
                    </div>
                  </v-card-title>
                  <v-card-text>
                    <v-data-table
                      hide-actions
                      :items="org.events"
                      :headers="eventHeaders"
                    >
                      <template slot="items" scope="props">
                        <td>{{ props.item.name }}</td>
                        <td>{{ props.item.type }}</td>
                        <td>{{ props.item.date }}</td>
                        <td>{{ props.item.location }}</td>
                        <td>{{ props.item.fbLinkh }}</td>
                        <td>{{ props.item.contact }}</td>
                      </template>
                    </v-data-table>
                  </v-card-text>
                </v-card>
              </v-flex>
            </v-card>
          </v-tabs-content>
        </v-tabs-items>
      </v-tabs>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'profile',
  computed: {
    org () {
      return this.$store.getters.currentOrg
    }
  },
  data () {
    return {
      officerHeaders: [
        {
          text: 'Positions',
          align: 'left',
          value: 'position'
        },
        {
          text: 'Name',
          align: 'left',
          value: 'name'
        },
        {
          text: 'Email',
          align: 'left',
          value: 'email'
        }
      ],
      eventHeaders: [
        {
          text: 'Event Name',
          align: 'left',
          value: 'eventName'
        },
        {
          text: 'Type',
          align: 'left',
          value: 'type'
        },
        {
          text: 'Date',
          align: 'left',
          value: 'data'
        },
        {
          text: 'Location',
          align: 'left',
          value: 'location'
        },
        {
          text: 'FB Event Link',
          align: 'left',
          value: 'fbLink'
        },
        {
          text: 'Point of Contact',
          align: 'left',
          value: 'contact'
        }
      ],
      
    }
  }
}
</script>



