<template>
   <v-container>
    <v-layout row wrap>
      <v-flex xs12 md6 offset-md3 class="text-md-center">
        <v-card md12 raised>
          <v-card-text>
              <h3>Login</h3>
              <v-form @submit.prevent="signin">
                <v-text-field
                label="Email"
                v-model="email"
                required
                ></v-text-field>

                <v-text-field
                label="Password"
                v-model="password"
                required
                ></v-text-field>

                <v-btn dark @click="signin" :class="{ green: valid, red: !valid }">Login</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      email: '',
      password: ''
    }
  },
  computed: {
    currentOrg () {
      return this.$store.getters.currentOrg
    }
  },
  watch: {
    currentOrg (value) {
      if (value !== null && value !== undefined) {
        this.$router.push('/')
      }
    }
  },
  methods: {
    signin () {
      this.$store.dispatch('signInAccount', {
        email: this.email,
        password: this.password
      })
    }
  }
}
</script>




