<template>
     <v-container>
    <v-layout row>
      <v-flex xs12 md6 offset-md3 class="text-md-center">
        <v-card md12 raised>
          <v-card-text>
              <h3>Register an Account</h3>
              <v-alert info value="true">
                ACE is currently in an invite-only Alpha. Registration will be open when the Beta gets released.
              </v-alert>
              <v-form @submit.prevent="createNewOrg">
                <v-text-field
                name="accesscode"
                label="Access Code"
                id="accesscode"
                hint="ACE is currently in a closed Alpha"
                persistent-hint
                v-model="accessCode"
                type="text"
                required
                ></v-text-field>

                <v-text-field
                name="email"
                label="Email"
                id="email"
                hint="Don't use your personal email, use an email that your entire org uses"
                v-model="email"
                type="email"
                required
                ></v-text-field>

                <v-text-field
                name="password"
                label="Password"
                id="password"
                v-model="password"
                type="password"
                required
                ></v-text-field>

                <v-text-field
                name="confirmPassword"
                label="Confirm Password"
                id="confirmPassword"
                v-model="confirmPassword"
                type="password"
                :rules="[comparePasswords]"
                ></v-text-field>

                <v-btn
                  class="primary"
                  :disabled="!formValidation"
                  type="submit"
                >Register</v-btn>
            </v-form>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      accessCode: '',
      orgName: '',
      email: '',
      password: '',
      confirmPassword: '',
      description: ''
    }
  },
  computed: {
    formValidation () {
      return this.email.length > 4
        && this.password.length > 2
        && this.password === this.confirmPassword
    },
    comparePasswords () {
      return this.password !== this.confirmPassword ? 'Passwords do not match' : ''
    },
    currentOrg () {
      return this.$store.getters.currentOrg
    }
  },
  watch: {
    currentOrg (value) {
      if (value !== null && value !== undefined) {
        this.$router.push('/')
      }
    }
  },
  methods: {
    createNewOrg () {
      this.$store.dispatch('createAccount', {
        email: this.email,
        password: this.password
      })
    }
  }
}
</script>



