import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'

import Profile from '@/components/Users/Profile'
import CreateProfile from '@/components/Users/CreateProfile'
import LoginProfile from '@/components/Users/LoginProfile'

import Events from '@/components/Events/Events'

import Resources from '@/components/Resources/Resources'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Resources
    },
    {
      path: '/register',
      name: 'Register',
      component: CreateProfile
    },
    {
      path: '/login',
      name: 'Login',
      component: LoginProfile
    },
    {
      path: '/events',
      name: 'Events',
      component: Events
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/resources',
      name: 'Resources',
      component: Resources
    }
  ],
  mode: 'history'
})



