import Vue from 'vue'
import Vuex from 'vuex'
import * as firebase from 'firebase'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state: {
        user: null,
        events: {
            allEvents: [
                {
                    name: '',
                    org: '',
                    description: '',
                    date: '',
                    time: '',
                    location: '',
                    collab: false,
                    collabDescription: '',
                    tags: []
                }
            ]
        },
        error: ''
    },
    mutations: {
        setOrg (state, payload) {
            state.user = payload
        }
    },
    actions: {
        createAccount ({commit}, payload) {
            firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
                .then(
                    user => {
                        const newOrg = {
                            id: user.uid,
                            name: '',
                            nameShort: '',
                            email: user.email,
                            clubroom: '',
                            image: '',
                            summary: '',
                            boardMeeting: {},
                            officers: [],
                            events: []
                        }
                        commit('setOrg', newOrg)
                    }
                )
                .catch(
                    error => {
                        console.log("Error occured when creating new account")
                        console.log(error)
                        state.error = error
                    }
                )
        },
        signInAccount ({commit}, payload) {
            firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
                .then(
                    user => {
                        const newOrg = {
                            id: user.uid,
                            name: '',
                            nameShort: '',
                            email: user.email,
                            clubroom: '',
                            image: '',
                            summary: '',
                            boardMeeting: {},
                            officers: [],
                            events: []
                        }
                        commit('setOrg', newOrg)
                    }
                )
                .catch(
                    error => {
                        console.log(error)
                    }
                )
        }
    },
    getters: {
        currentOrg (state) {
            return state.user
        },
        // TODO: Prompt the error to the user
        getError (state) {
            return state.error.length
        },
    }
})


